"use client";

import React, { useState } from "react";
import { useResumeStore } from "@/store/useResumeStore";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Button } from "@/components/ui/button";
import { Card, CardContent } from "@/components/ui/card";
import { Plus, Sparkles, Loader2, Mail, User } from "lucide-react";

export default function ResumeBuilder() {
  const { resume, setPersonalInfo, addExperience, updateExperience } = useResumeStore() as any;
  const [isOptimizing, setIsOptimizing] = useState<number | null>(null);

  // AI 润色处理函数
  const handleOptimize = async (index: number, currentText: string) => {
    if (!currentText || currentText.trim().length < 5) {
      alert("请多输入一点工作描述（至少5个字），AI 才能精准润色哦！");
      return;
    }
    
    setIsOptimizing(index);
    try {
      const res = await fetch("/api/optimize", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ text: currentText }),
      });
      const data = await res.json();
      updateExperience(index, "description", data.result);
    } catch (error) {
      console.error("AI 润色请求失败:", error);
      alert("网络连接有点问题，请稍后再试。");
    } finally {
      setIsOptimizing(null);
    }
  };

  return (
    <div className="flex h-screen bg-zinc-50">
      {/* 左侧：内容输入区 */}
      <div className="w-1/2 p-10 overflow-y-auto border-r border-zinc-200">
        <div className="max-w-xl mx-auto space-y-12">
          <header>
            <h1 className="text-4xl font-extrabold tracking-tight text-zinc-900">PathResume</h1>
            <p className="text-zinc-500 mt-3 text-lg">打造一份能拿到面试邀请的专业简历。</p>
          </header>

          {/* 个人信息 */}
          <section className="space-y-6">
            <h2 className="text-xl font-bold flex items-center gap-2 text-zinc-800">
              <User className="w-5 h-5 text-blue-600" /> 个人信息
            </h2>
            <div className="grid grid-cols-2 gap-6">
              <div className="space-y-2">
                <Label className="text-zinc-600">姓名</Label>
                <Input
                  className="bg-white"
                  placeholder="例如：张三"
                  value={resume.personalInfo.fullName}
                  onChange={(e) => setPersonalInfo({ ...resume.personalInfo, fullName: e.target.value })}
                />
              </div>
              <div className="space-y-2">
                <Label className="text-zinc-600">邮箱</Label>
                <Input
                  className="bg-white"
                  placeholder="zhangsan@example.com"
                  value={resume.personalInfo.email}
                  onChange={(e) => setPersonalInfo({ ...resume.personalInfo, email: e.target.value })}
                />
              </div>
            </div>
          </section>

          {/* 工作经历 */}
          <section className="space-y-6">
            <div className="flex justify-between items-center">
              <h2 className="text-xl font-bold flex items-center gap-2 text-zinc-800">
                <Mail className="w-5 h-5 text-blue-600" /> 工作经历
              </h2>
              <Button onClick={addExperience} variant="outline" size="sm" className="bg-white hover:bg-blue-50 border-blue-200 text-blue-600 transition-colors">
                <Plus className="w-4 h-4 mr-1" /> 添加条目
              </Button>
            </div>

            {resume.experience.map((exp: any, index: number) => (
              <Card key={index} className="border-zinc-200 shadow-sm transition-all hover:shadow-md">
                <CardContent className="pt-6 space-y-5">
                  <div className="grid grid-cols-2 gap-4">
                    <div className="space-y-2">
                      <Label>公司/组织</Label>
                      <Input
                        placeholder="公司名称"
                        value={exp.company}
                        onChange={(e) => updateExperience(index, "company", e.target.value)}
                      />
                    </div>
                    <div className="space-y-2">
                      <Label>职位</Label>
                      <Input
                        placeholder="例如：高级产品经理"
                        value={exp.role}
                        onChange={(e) => updateExperience(index, "role", e.target.value)}
                      />
                    </div>
                  </div>
                  
                  <div className="space-y-3">
                    <div className="flex justify-between items-center">
                      <Label className="text-sm font-medium">工作描述</Label>
                      <Button
                        variant="ghost"
                        size="sm"
                        className="text-purple-600 hover:text-purple-700 hover:bg-purple-50 font-semibold"
                        onClick={() => handleOptimize(index, exp.description)}
                        disabled={isOptimizing === index}
                      >
                        {isOptimizing === index ? (
                          <Loader2 className="w-4 h-4 mr-1 animate-spin" />
                        ) : (
                          <Sparkles className="w-4 h-4 mr-1" />
                        )}
                        AI 智能润色
                      </Button>
                    </div>
                    <textarea
                      className="w-full p-4 border rounded-xl text-sm h-36 focus:ring-2 focus:ring-blue-500 border-zinc-200 outline-none transition-all resize-none bg-zinc-50"
                      placeholder="请简单列出你的工作内容或项目成就..."
                      value={exp.description}
                      onChange={(e) => updateExperience(index, "description", e.target.value)}
                    />
                  </div>
                </CardContent>
              </Card>
            ))}
          </section>
        </div>
      </div>

      {/* 右侧：A4 纸实时预览 */}
      <div className="w-1/2 bg-zinc-200 p-12 flex justify-center overflow-y-auto">
        <div className="w-[210mm] min-h-[297mm] h-fit bg-white p-[25.4mm] shadow-[0_20px_50px_rgba(0,0,0,0.15)] flex flex-col box-border">
          {/* 简历头部 */}
          <div className="text-center space-y-3 border-b-2 border-zinc-900 pb-8">
            <h1 className="text-4xl font-extrabold uppercase tracking-[0.2em] text-black">
              {resume.personalInfo.fullName || "你的姓名"}
            </h1>
            <p className="text-sm text-zinc-600 font-medium tracking-wide">
              {resume.personalInfo.email || "email@example.com"}
            </p>
          </div>

          {/* 经历板块预览 */}
          <div className="mt-10">
            <h3 className="text-lg font-black border-b border-zinc-300 mb-6 pb-1 tracking-wider text-zinc-900">WORK EXPERIENCE</h3>
            {resume.experience.length === 0 ? (
              <div className="text-zinc-300 text-center py-20 border-2 border-dashed border-zinc-100 rounded-lg">
                在左侧添加你的工作经历，简历会自动在这里排版
              </div>
            ) : (
              resume.experience.map((exp: any, i: number) => (
                <div key={i} className="mb-8 group">
                  <div className="flex justify-between items-baseline mb-2">
                    <span className="font-bold text-lg text-black">{exp.company || "公司名称"}</span>
                    <span className="text-sm font-semibold text-zinc-700 italic">{exp.role || "职位信息"}</span>
                  </div>
                  <p className="text-[14px] leading-[1.6] text-zinc-700 whitespace-pre-line text-justify font-serif">
                    {exp.description || "工作描述待填写..."}
                  </p>
                </div>
              ))
            )}
          </div>
        </div>
      </div>
    </div>
  );
}